/*
*编辑框
*/

<template xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div>
    <!--非标准地址列表-->
    <div class="listView">
      <!--地址匹配-->
      <div class="selector flex_row" v-if="!username">
        <div class="block flex_row">
          <!--选择街道-->
          <span class="label">街道：</span>
          <select v-model="curStreet" class="select" @change="onSelStreet">
            <option v-for="s in allStreets">
              {{s}}
            </option>
          </select>
        </div>

        <div class="block flex_row">
          <span class="label">社区：</span>
          <!--选择社区-->
          <select v-model="curCommunity" class="select" @change="onSelStreet">
            <option v-for="c in allCommunity">
              {{c.community}}
            </option>
          </select>
        </div>
        <div class="flex_row toggle">
          <span v-show="showList" @click="showList=!showList">▲</span>
          <span v-show="!showList" @click="showList=!showList">▼</span>
        </div>
      </div>


      <!--地址编辑-->
      <div class="selector flex_row" v-if="username">
        <div class="block flex_row editBlock">
          <!--选择街道-->
          <span class="label">数据源：</span>
          <Select v-model="curTarget" class="select" @on-change="onSelTarget" :title="curTarget">
            <Option  v-for="t,ti in targetList" :key='ti' :title="t" class="ellipsis" :value="t">
              {{t}}
            </option>
          </Select>
        </div>
      </div>
      <!--loading spin-->
      <small-spin v-if="needSpin" msg="正在加载……"></small-spin>

      <!--列表-->
      <ul v-show="showList" class="siteList">
        <li class="flex_row" @click="toggleEditModal(l.fid,index)" v-for="(l,index) in listArr"
            :class="{sel:curEditIndex==index}"
        >
          <span>{{l.origin_address}}</span>
          <img src="../../static/images/map/edit.png" class="imgItem">
        </li>
        <Page :current='page' :total="listCount" show-total size="small" class-name="pageClass" @on-change="pageChanged"/>
      </ul>

    </div>

    <!--地址匹配对话框-->
    <Modal
      v-model="showEditModal"
      title="地址编辑"
      :mask="false"
      :styles="{
          width: '320px',
          position: 'absolute',
          top: '75px',
          right: '20px',
          maxHeight:'80%'}"
      scrollable :visible="editVisible"
    >

      <div v-for="(val,index) in editObj" class="editLine flex_row"
           :class="{autoHeightWrap:val.type=='textarea'}" v-if="username?val.needEdit:true">
        <div class="label flex_row">
          <span v-html="val.label"></span>
        </div>


        <input type="text" v-model="editValue[val.val]" :title="editValue[val.val]"
               :class="val.disabled?'text':'text canEdit'"
               :disabled="val.disabled" v-if="val.type=='text'"/>
        <div class="autoHeight"
             v-if="val.type=='textarea'">
          {{editValue[val.val]}}
        </div>

      </div>


      <!--相似标准地址-->
      <div class="editLine flex_row  autoHeightWrap">
        <div class="label flex_row">
          <span>相似标准地址</span>
        </div>
        <div class="selectWrapper autoHeight flex_row">
          <Select v-model="curSelAddress.address" class="select"
                  placeholder="无匹配数据"
          >
            <Option v-for="(s,index) in matchResult" :value="s.address" :key="index"
                    :label="s.address">
              <span>{{ s.address }}</span>
              <Progress
                :stroke-width="7" title="相似度"
                :percent="s.similarity*100" class="similar_row"/><!--相似度 similarity-->
            </Option>

          </Select>
          <div class="editBtnWrap" v-if="username" >
            <Button shape="circle" icon="md-create" title="编辑" size="small" @click="showEditWin=true"></Button>
          </div>
        </div>
      </div>

      <!--选择结果-->
      <div class="editLine flex_row autoHeightWrap">
        <div class="label flex_row">
          <span>标准地址</span>
        </div>

        <div class="autoHeight flex_row" v-if="curSelAddress.address">
          <span>{{curSelAddress.address}}</span>
          <Progress vertical :stroke-width="7" title="相似度"
                    :percent="curSelAddress.similarity*100" class="similar"/><!--相似度 similarity-->
        </div>
        <div v-if="!curSelAddress.address" class="autoHeight flex_row" >无匹配数据</div>
      </div>

      <!--按钮-->
      <div slot="footer" class="">
        <Button size="small" @click="backRecord"
                :disabled="page==1&&curEditIndex==0"
        >
          <Icon type="ios-arrow-back"></Icon>
        </Button>
        <Button v-if="!username">取消</Button>
        <Button type="primary" v-if="!username">保存</Button>
        <Button size="small" @click="nextRecord"
                :disabled="(curEditIndex==listCount%10-1||curEditIndex==9)&&(page==Math.ceil(listCount/10))"

        >
          <Icon type="ios-arrow-forward"></Icon>
        </Button>
      </div>

    </Modal>

    <!--地址编辑窗-->
    <Modal
      v-model="showEditWin"
      title="地址编辑"
      :styles="{
          width: '320px',
          position: 'absolute',
          top: '75px',
          right: '20px',
          maxHeight:'80%'}"
      scrollable
    >

      <div v-for="(val,index) in addEditObj" class="editLine flex_row"
           >
        <div class="label flex_row">
          <span v-html="val.label"></span>
        </div>

        <input type="text" v-model="addEditValue[val.val]" :title="addEditValue[val.val]"
               :class="val.canInput?'text canEdit':'text canAddEdit'"
               :disabled="!val.canInput"/>
        <Icon type="ios-arrow-down" style='cursor: pointer' size='20' v-if="showOption!=index" @click="showOption=index"/>
        <Icon type="ios-arrow-up" style='cursor: pointer' size='20' v-if="showOption==index" @click="showOption=-1"/>

        <div class="options" v-if="showOption==index">
          <RadioGroup v-model="addEditValue[val.val]" vertical>
            <Radio :label="o" v-for="(o,i) in val.options" :key="i">
              {{o}}
            </Radio>
          </RadioGroup>
        </div>
      </div>
      <!--按钮-->
      <div slot="footer" class="">
        <Button @click="showEditWin=false">取消</Button>
        <Button type="primary" @click="submitEdit">保存</Button>
      </div>

    </Modal>
  </div>

</template>


<script>
  import SmallSpin from "./smallSpin";
  import { mapGetters,mapActions } from 'vuex'
  export default {
    name: 'editBox',
    data() {
      return {
        needUser: false,

        needSpin: false,//显示spin
        page: 1,//当前页
        curStreet: '',//当前选择的街道
        allStreets: EDITSELECTORCFG['street'],
        curCommunity: '',//当前选择的社区
        allCommunity: [],


        showList: false,//打开非标准地址列表
        //非标准地址列表
        listArr: [],
        listCount: 0,//非标准地址总数，用于分页
        curEditIndex:-1,//当前选择结果索引


        showEditModal: false,//是否打开编辑浮云
        //编辑对象
        editVisible: false,
        editObj: [
          {'label': '统一社会信用代码', val: 'code', 'disabled': true, 'necessary': false, type: 'text',needEdit:false},
          {'label': '企业名称', val: 'name', 'disabled': true, 'necessary': false, type: 'text',needEdit:false},
          {'label': '街道', val: 'street', 'disabled': true, 'necessary': false, type: 'text',needEdit:true},
          {'label': '社区', val: 'community', 'disabled': true, 'necessary': false, type: 'text',needEdit:true},
          {'label': '原地址', val: 'originAddress', 'disabled': true, 'necessary': false, type: 'textarea',needEdit:true},//JYCS字段

        ],
        editValue: {
          code: '',
          name: '',//企业名称
          street: '',//街道
          community: '',//社区
          originAddress: ''//原地址
        },

        matchResult: [],//相似标准地址
        curSelAddress: {},//当前选择的匹配结果


        //-------------地址编辑
        curTarget:'',
        targetList:[],//数据源列表
        showEditWin:false,//打开编辑框
        addEditObj:[
          {'label': '楼栋编码', val: 'code', 'necessary': false, options:['选项1','2'],canInput:true},
          {'label': '街道', val: 'street', 'necessary': false, options:['选项1','2'],canInput:false},
          {'label': '社区', val: 'community', 'necessary': false, options:['选项1','2'],canInput:false},
          {'label': '小区', val: 'neibor', 'necessary': false, options:['选项1','2'],canInput:true},
          {'label': '楼栋', val: 'building', 'necessary': false, options:['选项1','2'],canInput:true},
        ],
        addEditValue:{
          code:null,
          street:null,
          community:null,
          neibor:null,
          building:null,
        },
        showOption:-1
      }
    },
    components: {SmallSpin},
    computed: {
      ...mapGetters(['username','needRefreshTarget'])
    },
    props: {},
    created() {
    },
    mounted() {

    },
    methods: {
      ...mapActions(['updateTablename']),
      //监听到页码变化
      pageChanged(page,option) {
        option=option||{needSelFirst: true};
        var $this = this;
        $this.page = page;
        if(!$this.username){//地址匹配状态
          //调用接口获取当前街道当前页，社区下的所有非标准地址
          $this.getAddressByCommunity(false,option)
        }else{
          //地址编辑状态
          $this.getAddByTarget(page,option)
        }

      },

      //地址编辑，选择数据源时
      onSelTarget(){
        var curTarget = this.curTarget;
        var $this = this;
        if (curTarget) {
          $this.updateTablename(curTarget);

          //切换到地址编辑，更新列表信息
          $this.curEditIndex=-1;
          $this.showList=false;
          $this.needSpin = true;//显示正在加载


          //设置工作数据表
          $this.$api.setTarget(curTarget,$this.username).then(function (res) {
            $this.$Message.success('数据源切换成功！');
            //查询总数
            // var tablename = curTarget.split('.')[0];//截取
            $this.$api.getCount_edit($this.username).then(function (res) {
              $this.listCount = res.total;//总数
              //将页面恢复到第1页
              $this.page = 1;
              $this.getAddByTarget($this.page)


            }).catch(function () {
              $this.needSpin = false;//显示正在加载
              $this.listArr=[];
              $this.showList = false;
            });
          }).catch(function (err) {
            if(err.response.data){
              $this.$Message.warning(err.response.data+',切换失败!')
            }
            $this.listArr=[];
            $this.showList = false;
            $this.needSpin = false;//显示正在加载
          })



        }
      },
      getAddByTarget(page,option){
        option=option||{needSelFirst: true};
        var $this = this;
        //查询当前页列表
        $this.$api.getAddressByTarget(page,$this.username).then(function (res) {
          $this.needSpin = false;//显示正在加载
          $this.listArr = res.rows;
          $this.showList = true;
          if (option.needSelFirst) {
            //需要默认选择第一项
            $this.toggleEditModal(res.rows[0]['fid'], 0)
          } else if (option.needSelLast) {
            //需要默认选择最后一项
            $this.toggleEditModal(res.rows[9]['fid'], 9)
          }
        }).catch(function (err) {
          $this.needSpin = false;//显示正在加载
          $this.listArr=[];
          $this.showList = true;
        });
      },
      //选择街道时
      onSelStreet() {
        var curStreet = this.curStreet;
        var $this = this;
        if (curStreet) {
          //调用接口获取当前街道含有的所有社区
          var url = URLCFG['getCommunityByStreetUrl'];
          $this.$api.getMsg(url, {tablename: curStreet}).then(function (res) {
            $this.allCommunity = res.rows;
            $this.curCommunity = res['rows'][0]['community'];
            //调用接口获取当前街道，社区下的所有非标准地址
            $this.getAddressByCommunity(true,{needSelFirst:true})
          })

        }

      },
      //当选择社区时
      onSelCommunity() {
        var curCommunity = this.curCommunity;
        var $this = this;
        if (curCommunity) {
          $this.getAddressByCommunity(true,{needSelFirst:true})
        }

      },
      //调用接口获取当前街道，社区下的所有非标准地址
      getAddressByCommunity(needTotal,option) {
        var $this = this;
        option=option||{};
        $this.curEditIndex=-1;
        $this.showList=false;
        $this.needSpin = true;//显示正在加载

        needTotal = needTotal == undefined ? true : needTotal;//翻页的时候可以不必查询总数
        if (needTotal) {
          //查询总数
          var url = URLCFG['getCountByCommunityUrl'];
          $this.$api.getMsg(url, {tablename: $this.curCommunity}).then(function (res) {
            $this.listCount = res.total;//总数
          });
          //将页面恢复到第1页
          $this.page=1;
        }
        //查询当前页列表
        $this.$api.getAddressByCommunity($this.page, $this.curCommunity).then(function (res) {
          $this.needSpin = false;//显示正在加载
          $this.listArr = res.rows;
          $this.showList = true;
          if (option.needSelFirst) {
            //需要默认选择第一项
            $this.toggleEditModal(res.rows[0]['fid'], 0)
          } else if (option.needSelLast) {
            //需要默认选择最后一项
            $this.toggleEditModal(res.rows[9]['fid'], 9)
          }
        });
      },
      //编辑开关
      toggleEditModal(fid,index) {
        var $this = this;
        if(!$this.username){
          $this.curSelAddress={};
          //调用接口查询编辑信息
          $this.$api.getEditMsg(fid, $this.curCommunity).then(function (res) {
            $this.editValue = {
              code: res.rows[0]['code'],
              name: res.rows[0]['name'],
              street: res.rows[0]['street'],
              // community: res.rows[0]['community'],
              community: $this.curCommunity,
              originAddress: res.rows[0]['address']
            };
            $this.showEditModal = true;
            $this.curEditIndex = index;//记录当前索引，方便下一条

            //调用接口获取匹配地址
            var keyword = res.rows[0]['address'];
            $this.$api.getMatchList( keyword).then(function (res) {
              if(res.rows&&res.rows.length>0){
                $this.matchResult = res.rows;
                $this.curSelAddress = res.rows[0];//默认选择第一个
              }else{
                //没有匹配结果
                $this.matchResult=[];
                $this.curSelAddress={}
              }
            });

          });
        }else{
          //调用接口查询编辑信息
          $this.$api.getInfoByFid_edit(fid, $this.username).then(function (res) {
            $this.editValue = {
              street: res.rows[0]['street']?res.rows[0]['street']:'',
              community: res.rows[0]['community']?res.rows[0]['community']:'',
              originAddress: res.rows[0]['origin_address']
            };
            $this.showEditModal = true;
            $this.curEditIndex = index;//记录当前索引，方便下一条
          });
        }


      },

      //上一条
      backRecord() {
        if (this.curEditIndex > 0) {
          var index = this.curEditIndex - 1;
          var fid = this.listArr[index]['fid'];
          this.toggleEditModal(fid, index);
        } else if (this.page > 1) {
          //执行向前翻页
          this.pageChanged(this.page - 1, {needSelLast:true});
          this.curEditIndex = 9
        }
      },
      //下一条
      nextRecord(){
        var pageCount = Math.ceil(this.listCount/10);//总页数
        if(this.page<pageCount){
          //非最后一页
          if(this.curEditIndex<9) {
            var index = this.curEditIndex + 1;
            var fid = this.listArr[index]['fid'];
            this.toggleEditModal(fid, index);
          }else{
            //往后翻一页
            this.pageChanged(this.page+1,{needSelFirst:true});
            this.curEditIndex = 0
          }
        }else if((this.listCount%10==0&&this.curEditIndex<9)||(this.curEditIndex<this.listCount%10-1)){
          //是最后一页
          //非最后一条时可以点击下一条
          var index = this.curEditIndex + 1;
          var fid = this.listArr[index]['fid'];
          this.toggleEditModal(fid, index);
        }

      },


      //提交地址编辑(地址匹配)
      submitEdit() {
        var $this = this;
        var addEditValue = $this.addEditValue;
        var editDict = EDITSERVICE['editDict'];//字典
        var editObj = {}
        for (var k in addEditValue){
          if (editDict[k]){
            editObj[editDict[k]] = addEditValue[k]
          }
        }
        var fid = $this.listArr[$this.curEditIndex]['fid'];
        $this.$api.setEdit(fid,$this.username,editObj).then(function (res) {
          if(res.update=='ok'){
            $this.$Message.success('保存成功！');
            $this.showEditWin = false
          }else{
            $this.$Message.warning('保存失败，请重试！')
          }
        }).catch(function (err) {
          $this.$Message.warning('保存失败，请重试！')
        })
      },

    },
    beforedestroy() {

    },
    watch: {
      needRefreshTarget: {
        //变化时刷新target列表
        handler(val) {
          var $this = this;
          if (val) {
            $this.curTarget = '';
            //查询数据源列表
            $this.$api.getTargetList($this.username).then(function (res) {
              if (res) {
                $this.targetList = res;
                $this.curEditIndex = -1;
                $this.showList = false;
              } else {
                $this.$Message.error('获取可下载的数据文件失败');
                $this.targetList = [];
              }
            }).catch(function (err) {
              $this.$Message.error('获取可下载的数据文件失败');
              $this.targetList = [];
            });
          } else {
          }
        },
        immediate: false
      }
    }
  }
</script>

<style scoped>
  .listView {
    width: 380px;
  }

  .listView .selector {
    margin-top: 20px;
    width: 100%;
    background: #3385ff;
    color: #fff;
    border-radius: 2px;
    box-shadow: 1px 2px 1px rgba(0, 0, 0, .15);
    height: 40px;
  }

  .selector .block {
    width: 50%;
    padding: 0 2%;
  }

  .selector .block:first-child {
    border-right: 1px solid #f2f2f2;
  }
  .selector .editBlock{
    border: none !important;
    width: 100%;
  }
  .selector .select {
    border-color: #fff;
    padding: 2px;
    width: 60%;
    border-radius: 3px;
  }

  .selector .toggle {
    flex-shrink: 0;
    padding: 0 10px;
    border-left: 1px solid #fff;
    cursor: pointer;
  }

  /*非标准地址列表*/
  .siteList {
    background: #fff;
    margin-top: 10px;
    border-radius: 4px;
    width: 100%;
    padding: 0 0 10px;
  }

  .siteList li {
    height: 45px;
    justify-content: flex-start;
    cursor: pointer;
    padding: 0 10px;
  }

  .siteList .sel,.siteList li:hover {
    color: #fff;
    background: rgba(0, 0, 0, 0.2);
  }

  .siteList li span {
    width: 100%;
    padding: 2px 0;
  }

  .siteList li .imgItem {
    flex-shrink: 0;
    width: 20px;
    margin-left: 10px;
  }

  /*分页*/
  .pageClass {
    padding: 5px 10px 0;
    border-top: 1px solid #f2f2f2;
  }


  /*编辑对话框*/
  .editModal .ivu-modal {
    position: absolute;
    top: 120px;
    right: 0;
  }

  .editLine {
    justify-content: flex-start;
    width: 100%;
    height: 35px;
    border-bottom: 2px solid #f2f2f2;
    position: relative;
  }

  .editLine .label {
    background: #3385ff;
    color: #fff;
    height: 100%;
    width: 100px;
    justify-content: center;
    flex-shrink: 0;
  }

  .editLine .canEdit {
    border: 1px solid #7f7f7f;
  }
  .editLine .canAddEdit{
    border: 1px solid #E8EBED ;
  }
  .editLine .text {
    background: none;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .autoHeightWrap .label {
    /*height: auto;*/
    /*min-height: 35px;*/
  }
  .autoHeightWrap{
    height: auto;
    background:#3385ff ;
  }
  .editLine .autoHeight {
    height: auto;
    min-height: 35px;
    padding-left: 10px;
    width: 100%;
    background: #fff;
    justify-content: flex-start;
  }

  .editLine .text, .editLine .textarea{
    margin-left: 10px;
    height: 80%;
    width: 100%;
  }

  .selectContainer {
    height: 45px;
  }

  .editLine .selectWrapper {
    position: relative;
    padding: 5px 0 10px 5px;
  }
  /*相似度*/
  .similar{
    border-left: 1px solid #f3f3f3;
    color: #3385ff;
    text-align: center;
  }
  .similar_row{
    border-top: 1px solid #f3f3f3;
    color: #3385ff;
    text-align: center;
  }
  .editLine .select {
    width: 100%;
    height: auto;
  }

  .editLine:last-child{
    border: none;
  }


  /*编辑*/
  .editLine .editBtnWrap{
    margin-left: 5px;
  }
  .editLine .options{
    position: absolute;
    top: 32px;
    right: 5px;
    background: #fff;
    padding: 0 5px;
    border: 1px solid #969696;
    z-index: 1;
    border-radius: 5px;
    width: 60%;
  }
</style>



